/* ============================================================================
Navigation
============================================================================ */
.demos-nav {
  background-color: tomato;
  color: #fff;
  line-height: 1.5;
  padding: 15px;
  width: 100%;
}

.demos-nav h1 {
  font-size: 110%;
  font-weight: bold;
  margin-bottom: .5em;
}

.demos-nav h1:not(:first-of-type) {
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  margin-top: 0.75em;
  padding-top: 0.75em;
}

.demos-nav__list {
  column-count: 2;
  column-gap: 2em;
}

@media screen and (min-width: 768px) {
  .demos-nav__list {
    column-count: 3;
  }
}

@media screen and (min-width: 1024px) {
  .demos-nav__list {
    column-count: 4;
  }
}

.demos-nav a {
  color: #fff;
  display: block;
  font-size: 0.9375rem;
  margin-bottom: 0.4em;
}

.demos-nav a:focus,
.demos-nav a:hover {
  text-decoration: underline;
}

.demos-nav a:last-of-type {
  margin-bottom: 0;
}

.js .demos-nav {
  left: 0;
  position: fixed;
  transform: translateY(-100%);
  transition: transform 100ms;
  z-index: 10;
}

.js .demos-nav.is-visible {
  transform: none;
}

.no-js .demos-nav__toggle {
  display: none;
  visibility: hidden;
}

.js .demos-nav__toggle {
  background-color: tomato;
  border: none;
  border-radius: 0 0 3px 3px;
  color: #fff;
  display: block;
  font-size: 12px;
  left: 50%;
  opacity: 0.25;
  padding: 2px 8px;
  position: absolute;
  text-transform: uppercase;
  top: 100%;
  transform: translateX(-50%);
  transition: opacity 100ms, padding-top 100ms;
}

.js .demos-nav__toggle:hover {
  cursor: pointer;
}

.js .demos-nav:not(.is-visible) .demos-nav__toggle:hover {
  padding-top: 10px;
}

.js .demos-nav:not(.is-visible) .demos-nav__toggle:hover,
.js .demos-nav.is-visible .demos-nav__toggle {
  opacity: 1;
}

